@use "../../scss/abstracts";

.wp-block-md-efi-fse-full-sample-block {
  background-color: abstracts.$green-theme-color;
  color: abstracts.$white-theme-color;
  padding: 5px 10px;
}

.md_customer_stories_section {
  padding: 50px 0;
  .md_customer_stories_section__heading {
    display: flex;
    justify-content: space-between;
    .md_customer_stories_section__heading__avatars {
      width: 20%;
      .md_customer_stories_grid_item__header__authoImage {
        width: 173px;
        height: 100%;
        img {
          border-radius: 50%;
          overflow: hidden;
          width: 100%;
          height: 100%;
        }
      }
    }
    h2 {
      width: 30%;
      font-weight: 500;
      line-height: 1.2em;
      margin: 0 0 20px;
      color: #000;
      font-size: 32px;
    }
    .md_customer_stories_section__projectSection {
      width: 20%;
      h5 {
        font-size: 50px;
        font-weight: 700;
        line-height: 1.2em;
        margin: 0;
      }
      p {
        font-size: 16px;
        font-weight: 400;
        line-height: 1.5em;
        margin: 0;
      }
    }
    .md_customer_stories_section__ratingSection {
      width: 20%;
      display: flex;
      flex-direction: column;
      align-items: center;
      h3 {
        font-size: 50px;
        font-weight: 700;
        line-height: 1.2em;
        margin: 0;
      }
      p {
        font-size: 16px;
        font-weight: 400;
        line-height: 1.5em;
        margin: 0;
      }
      .md_customer_stories_section__ratingSection__ratingStars {
        color: #f1c40f;
      }
    }
  }
  .md_customer_stories_section__sliderItems {
    padding: 50px 0;
    .md_customer_stories_grid {
      .md_customer_stories_grid_item {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        padding: 45px;
        margin: 50px 0;
        .md_customer_stories_grid_item__inner {
          .md_customer_stories_grid_item__authorHeading {
            display: flex;
            align-items: center;
            gap: 20px;
            .md_customer_stories_grid_item__authoImage {
              width: 100px;
              height: 100px;
              img {
                border-radius: 50%;
                overflow: hidden;
                width: 100%;
                height: 100%;
              }
            }
            .md_customer_stories_grid_item__authorDetails {
              h3 {
                font-size: 18px;
                font-weight: 500;
                margin: 0;
              }
              p {
                font-size: 16px;
                font-weight: 400;
                margin: 0;
              }
            }
          }
          .md_customer_stories_grid_item__content {
            p {
              font-size: 17px;
              font-weight: 400;
              margin: 20px 0;
            }
          }
          .md_customer_stories_grid_item__rating {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .md_customer_stories_grid_item__ratingStars {
              width: 50%;
              display: flex;
              justify-content: center;
              gap: 5px;
              p {
                font-size: 15px;
                font-weight: 700;
                margin: 0;
              }
              i {
                font-size: 15px;
                color: #f1c40f;
              }
            }
            .md_customer_stories_grid_item__dateOfTestimonial {
              width: 50%;
              display: flex;
              justify-content: flex-end;
              p {
                font-size: 15px;
                font-weight: 400;
                margin: 0;
                background-color: #242E45;
                border-radius: 14px;
                padding: 5px 10px;
                text-align: right;
                display: inline-block;
              }
            }
          }
        }
      }
    }
  }
  .md_customer_stories_section__companyList {
    padding: 50px 0;
    .md_customer_stories_section__companyList__container {
      display: flex;
      gap: 20px;
      flex-wrap: wrap;
      justify-content: center;
      .md_customer_stories_grid_item {
        width: calc(100% / 3 - 20px);
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        padding: 20px 0;
        .md_customer_stories_grid_item__inner {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          .md_customer_stories_companyList__companyImage {
            width: 100%;
            max-width: 200px;
            overflow: hidden;
            text-align: left;
            padding: 30px 0;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .md_customer_stories_grid_item__content {
            display: flex;
            justify-content: center;
            gap: 2px;
            font-size: 16px;
            font-weight: 400;
            opacity: 0.7;
            border-top: 1px solid #000;
            width: 100%;
            .md_customer_stories_grid_item__subText {
              font-weight: 700;
              opacity: 1;
            }
          }
        }
      }
    }
  }
  .md_customer_stories_section__companyLogos {
    .md_customer_stories_section__companyLogos__container {
      display: flex;
      gap: 20px;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      .md_customer_stories_grid_item {
        width: calc(100% / 6 - 20px);
        img {
          width: 100%;
          height: auto;
        }
      }
    }
  }
}
